import mixinPaging from '@/components/mixin/paging'
export default {
	mixins: [mixinPaging],
	data() {
		return {
			uni,
			urlList: 'caseList',
			queryParams: {
				state: 3
			},
			swiperList: [
				{
					url: 'http://120.79.13.215/img/slide1.73e09e1d.jpg'
				},
				{
					url: 'http://120.79.13.215/img/slide2.0fbf4522.jpg'
				},
				{
					url: 'http://120.79.13.215/img/slide3.7a235263.jpg'
				}
			],
			verifyNotice: [],
			// 用户信息
			userInfo: {
				// 实名认证状态
				auth: 0
			}
		}
	},
	onPullDownRefresh () {
		this.list.paging.page = 1
		this.list.data = []
		this.getList(uni.stopPullDownRefresh)
	},
	onLoad () {
		// 获取列表
		this.getList()
		// 获取推送提示
		this.getVerifyNotice()
	},
	onShow () {
		// if (uni.getStorageSync('no_regi')) {
		// 	this.noRegi()
		// 	return
		// }
		this.getUserInfo()
	},
	methods: {
		// 获取推送提示
		getVerifyNotice () {
			// this.$JRequest('verifyNotice', data => {
			// 	this.verifyNotice = data
			// 	this.$refs.popup.open()
			// })
			this.$JRequest('verifyNotice', data => {
				if (data.length) {
					this.$JFn.showModal({
						content: '当前有新的案件动态，是否查看？',
						confirmText: '查看',
						success: res => {
							if (res.confirm) {
								uni.navigateTo({
									url: '/pages/my/law-case/index'
								})
							}
						}
					})
				}
			}, () => {})
		},
		// 忽略提示
		// ignoreNotice (id) {
		// 	this.$JRequest('ignoreNotice', '/' + id, data => {
		// 		this.$JFn.showSuccess('操作成功')
		// 		this.getVerifyNotice()
		// 	})
		// },
		// 获取用户信息
		getUserInfo () {
			if (uni.getStorageSync('userInfo')) {
				this.userInfo = JSON.parse(uni.getStorageSync('userInfo'))
				return
			}
			this.$JRequest('userInfo', data => {
				this.userInfo = data
				uni.setStorageSync('userInfo', JSON.stringify(data))
			})
		},
		// 点击实名认证按钮
		certificateBtn () {
			if (this.userInfo.auth === 0) {
				uni.navigateTo({ url: '/pages/my/certification/index?userId=' + userInfo.userId })
			} else {
				uni.navigateTo({ url: '/pages/my/person-info/index' })
			}
		}
	}
}
